import global from '../../utils/global';
import babyApi from '../../service/baby';
import authApi from '../../service/auth';
export default {
	data() {
		return {
			dialogShow:false,
			backUrl:""
		}
	},
	methods: {
		bindGetUserInfo(res){
			if (!res.detail.iv) {
			    uni.showToast({
					icon:"none",
			        title: '您取消了授权，登录失败'
			    });
			    return false;
			};
			this.init(res.mp.detail.userInfo);
		},
		//用户登录 初始化页面数据
		async init(userInfo){
			const user = {
				avatar:userInfo.avatarUrl,
				nickName:userInfo.nickName,
				sex:userInfo.gender
			};
			//获取code
			const isLogin = await authApi.doLogin();
			if(isLogin){
				//更新用户信息
				authApi.setUser(user).then(()=>{
					global.setUser(JSON.stringify(userInfo));
				});
				//获取宝宝列表
				babyApi.getBabyList().then(res=>{
					if(res.data && res.data.length <= 0){
						this.dialogShow = true;
					}else{
						this.cancel();
					}
				})
			}
			
		},
		cancel(){
			uni.reLaunch({
			    url:this.backUrl || '/pages/task/index'
			});
		},
		addBaby(){
			uni.navigateTo({
			    url:'/pages/baby/addBaby'
			});
		}
	},
	onLoad(option){
		if(option.backpath){
			this.backUrl = option.backpath;
		}
	}
}
